<script>

    export let input
    export let numLevels
	let level_descriptions = [
		"Die Organisation ist sich der Relevanz von Agilitätsfähigkeiten und deren Verankerung in den jeweiligen Agilitätsdimensionen bewusst. Das Ziel, den agilen Reifegrad zu erhöhen, ist kommuniziert und spiegelt sich in den Arbeitspraktiken und Ansätzen der Zusammenarbeit wieder.",
		"Die Organisation hat bereits erste Schritte zur Entwicklung von Agilitätskapazitäten unternommen und erfüllt damit das Ziel, den agilen Reifegrad zu erhöhen. Alle durchgeführten Aktivitäten folgen diesem Ziel und gewährleisten Kundenorientierung und -zufriedenheit.",
		"Die Organisation hat sich weiter auf die Realisierung von Agilitätsfähigkeiten konzentriert. Alle Aktivitäten sind auf den Kunden und die Schaffung von Kundennutzen ausgerichtet. Das Unternehmen nutzt den Wert der Ressourcen innerhalb und außerhalb des Unternehmens, um qualitativ hochwertige Produkte und Dienstleistungen zu liefern.",
		"Die Organisation hat sich in mehreren Dimensionen auf die Agilitätsfähigkeit konzentriert. Auf diese Weise kann die Organisation effektiv und produktiv auf Veränderungen reagieren. Die Organisation konzentriert sich auf ständige Verbesserung und Unsicherheitsmanagement.",
		"Die Organisation hat Agilität ganzheitlich in ihrer Strategie, ihrer Struktur, ihren Prozessen, ihrer Kultur, der Denkweise ihrer Mitarbeiter und ihrem Führungsansatz verankert. Die Organisation schafft ein konstantes, lebendiges Umfeld, um Agilität aufrechtzuerhalten und einen organisatorischen Lernansatz zu verfolgen."
	]

	let dimension_descriptions = {
		"Strategie und Zweck": "Strategie und Zweck umfasst den Zweck einer Organisation, ihre Vision und Mission sowie ihr Geschäftsmodell.",
		"Struktur": "Struktur umfasst die Organisationsstruktur, die Führung sowie die Funktionen und Rollen innerhalb der Organisation.",
		"Prozess": "Prozess umfasst die Prozessorganisation und den Informations- und Entscheidungsfluss innerhalb der Organisation",
		"IT": "Informationstechnologie umfasst die technische Infrastruktur und Architektur sowie die Anwendung der Technologie.",
		"Menschen": "Menschen & Kultur umfasst die Organisationskultur, das Verhalten und die Denkweise der einzelnen Mitarbeiter sowie deren Fähigkeiten.",
		"Führung": "Führung umfasst den Führungsansatz, die Führungswerte und -einstellung sowie die Befähigung der Mitarbeiter.",
		"Kundenzentrierte Produktentwicklung": "Kundenzentrierte Produktentwicklung umfasst den Ansatz, Produkte zu entwickeln, bei denen der Kunde im Mittelpunkt steht."
	}


    function processInput(event) {
		input[this.dataset.dimension][this.name].value = Number(this.value)
		console.log(input[this.dataset.dimension])
	}

</script>

<form>
	<label for="unternehmen">Unternehmen:</label>
	<input type="text" id="unternehmen" name="unternehmen" class="frontmatter">

	<label for="bereich">Organisation:</label>
	<input type="text" id="bereich" name="bereich" class="frontmatter">

	<label for="bereich">Name des agilen Projektes:</label>
	<input type="text" id="bereich" name="bereich" class="frontmatter">


	<label for="beschreibung">Beschreibung:</label>
	<input type="text" id="beschreibung" name="beschreibung" class="frontmatter">

</form>

{#each Object.entries(input) as [dimension, capabilities]}
    <div class="capability_container">

        <div class="titlebar">
            <h3 class="leveltooltip">{dimension}
				<span class="leveltooltiptext">{dimension_descriptions[dimension]}</span>
			</h3>
        </div>

        <div class="capabilities inline">
            {#each Object.entries(capabilities) as [capability, content]}
                <span class="capability_title tooltip">{capability} 
					<span class="tooltiptext">{content.description}</span>
				</span>
            {/each}
        </div>
    
        <div class="level_selection inline">
            <div class="level_list">
                {#each Array(numLevels) as _, i}
					<span class="level_title inline leveltooltip">Level {i + 1} 
						<span class="leveltooltiptext">{level_descriptions[i]}</span>
					</span>
					<!-- <div class="level_title inline">Level {i + 1}</div> -->
                {/each}
				<span class="level_title inline" style="width: 50%; text-align: left;">Begründung</span>
            </div>
            
            {#each Object.entries(capabilities) as [capability, content]}
                <!-- svelte-ignore a11y-label-has-associated-control -->
                <label class="level_radio">
                    {#each Array(numLevels) as _, i}
						{#if i+1 == input[dimension][capability].value}
                        <input type="radio" class="radio_input" name={capability} data-dimension={dimension} value={i+1} checked="checked" on:change={processInput}>
						{:else}
                        <input type="radio" class="radio_input" name={capability} data-dimension={dimension} value={i+1} on:change={processInput}>
						{/if}
                    {/each}
					<input type="text" style="width: 50%;">
                </label>
				
            {/each}
        </div>
    </div>
{/each}


<button>Send</button>

<style>
    .inline {
		display: inline-block;
	}

	.capabilities {
		width: 30%;
		text-align: right;
	}

	.level_selection {
		width: 60%;
		padding: 10px;
		/* margin-right: 20%; */
	}

	.level_radio {
		height: 50px;
		display: flex;
		justify-content:  space-between;
	}

	.level_radio input {
		margin: auto;
	}

	.radio_input {
		width: 10%;
	}

	.capability_title {
		position: relative;
		display: block;
		/* margin-right: 20px; */
		height: 50px;
	}

	.level_list {
		width: 100%;
		display: inline-flex;
		/* margin-right: 15%; */
	}

	.frontmatter {
		width: 30%;
		margin-top: 0.5em;
	}

	.level_title {
		margin: auto;
		width: 10%;
	}

	.titlebar h3 {
		margin: 0;
		margin-bottom: 10px;
		margin-top: 30px;
	}

	/* Tooltip text */
	.tooltip .tooltiptext {
		visibility: hidden;
		width: 100%;
		background-color: #555;
		color: #fff;
		text-align: center;
		padding: 5px 0;
		border-radius: 6px;

		/* Position the tooltip text */
		position: absolute;
		z-index: 1;
		bottom: 60px;
		left: 0%;

		/* Fade in tooltip */
		opacity: 0;
		transition: opacity 0.3s;
	}

	/* Tooltip arrow */
	.tooltip .tooltiptext::after {
		content: "";
		position: absolute;
		top: 100%;
		left: 80%;
		margin-left: -5px;
		border-width: 5px;
		border-style: solid;
		border-color: #555 transparent transparent transparent;
	}

	/* Show the tooltip text when you mouse over the tooltip container */
	.tooltip:hover .tooltiptext {
		visibility: visible;
		opacity: 1;
	}

	/* The same but for levels */

	/* Tooltip text */
	.leveltooltip .leveltooltiptext {
		visibility: hidden;
		width: 100%;
		background-color: #555;
		color: #fff;
		text-align: center;
		padding: 5px 0;
		border-radius: 6px;

		/* Position the tooltip text */
		position: fixed;
		z-index: 1;
		top: 10%;
		left: 0%;

		/* Fade in tooltip */
		opacity: 0;
		transition: opacity 0.3s;

		font-weight: normal;
	}

	/* Tooltip arrow */
/* 	.leveltooltip .leveltooltiptext::after {
		content: "";
		position: absolute;
		top: 100%;
		left: 80%;
		margin-left: -5px;
		border-width: 5px;
		border-style: solid;
		border-color: #555 transparent transparent transparent;
	} */

	/* Show the tooltip text when you mouse over the tooltip container */
	.leveltooltip:hover .leveltooltiptext {
		visibility: visible;
		opacity: 1;
	}

</style>